<template>
  <div style="padding-bottom:20px;background:#f1eff2;">
    <!-- <mu-appbar style="width: 100%;" color="#4ed995">
      <div slot="left" color="#3051f1">
        <a href="#/Navigation?id=2">
          <mu-button icon size="20">
            <mu-icon color="white" value="keyboard_arrow_left"></mu-icon>
          </mu-button>
        </a>
      </div>
      <div style="width:100%;text-align:center;">
        <span style="font-size:18px;color:white;">任务管理</span>
      </div>
    </mu-appbar> -->
    <van-nav-bar
      :fixed="true"
      title="任务管理"
      @click-left="()=>{
          $router.go(-1)
         }"
      left-arrow
      class="backgroundNaviBar"
      z-index="999"
      :border="false"
    >
      <!-- <van-icon name="search" slot="right" /> -->
    </van-nav-bar>
    <div class="contanerMain">
      <!-- <select name="" id=""></select> -->
      <mu-row>
        <mu-col span="3" lg="4" sm="6" class="flexCenterMainChild">时间：</mu-col>
        <mu-col span="9" lg="4" sm="6">
          <mu-date-input v-model="value3" label label-float full-width no-display>
            <!-- <mu-icon value=""></mu-icon> -->
          </mu-date-input>
        </mu-col>
      </mu-row>
      <mu-row class="MainList">
        <mu-col span="4">地点：</mu-col>
        <mu-col span="8"></mu-col>
      </mu-row>
      <mu-row class="MainList">
        <mu-col span="4">事件名称：</mu-col>
        <mu-col span="8"></mu-col>
      </mu-row>
      <mu-row class="MainList">
        <mu-col span="4">事件类型：</mu-col>
        <mu-col span="8"></mu-col>
      </mu-row>
      <mu-row class="MainList">
        <mu-col span="4">接收人：</mu-col>
        <mu-col span="8"></mu-col>
      </mu-row>
      <mu-row>
        <mu-col span="3" class="flexCenterMainChildTextValue">事件内容</mu-col>
        <mu-col span="12">
          <textarea name disabled id="textvalueMain" cols="40" rows="10"></textarea>
        </mu-col>
      </mu-row>
      <mu-row>
        <mu-col span="12">现场图片</mu-col>
        <mu-col span="12">
          <div class="addImageSize">
            <img src="../../../static/img/addImage.png" alt />
          </div>
        </mu-col>
      </mu-row>
      <mu-row class="MainList" style="margin-bottom:10px;">
        <mu-col span="3">发起人：</mu-col>
        <mu-col span="9"></mu-col>
      </mu-row>
    </div>

    <mu-col span="12" style="margin-top:20px;">
      <mu-button full-width color="#4ed99f">提交</mu-button>
    </mu-col>
  </div>
</template>
<script>
export default {
  name: "maintenanceChild3",
  data() {
    return {
      options: ["沙井一期进水口水质监测站"],
      value3: null,
      normal: {
        value1: null,
        value2: null
      },
      checkbox: {
        value1: [],
        value2: false,
        value3: false
      },
      checkboxContent: [
        { id: 1, content: "标液标准" },
        { id: 2, content: "标准曲线" },
        { id: 3, content: "精密度检查" }
      ],

      checkAll: true
    };
  },
  methods: {},
  mounted() {}
};
</script>
<style scoped>
.contanerMain {
  /* margin: 0 5px; */
  background: white;
  padding: 20px;
  /* border-radius: 10px; */
  /* margin-top: 103px; */
  /* margin-bottom: 50px; */
}
.flexCenterMain {
  display: flex;
  height: 40px;
  /* justify-content: center; */
  align-items: center;
}
.flexCenterMainChild {
  display: flex;
  height: 33px;
  align-items: center;
  font-size: 14px;
  border-bottom: 1px #f1eff2 solid;
}
.flexCenterMainChildTextValue {
  display: flex;
  height: 33px;
  align-items: center;
  font-size: 14px;
}
#textvalueMain {
  height: 80px;
  width: 100%;
  border: none;
  border-radius: 5px;
}
.MainList {
  border-bottom: 1px solid rgb(243, 243, 243);
  height: 50px;
  display: flex;
  /* align-items: center; */
  align-content: center;
  /* justify-content: center; */
}
.select-control-row {
  padding: 8px 0;
  color: blue;
  display: flex;
  flex-direction: column;
}
.select-control-group {
  margin: 16px 0;
  /* color: blue; */
}
.addImageSize img {
  /* width: 100px; */
}
</style>



